import Sprite from "../img/Sprites.png";

const game = document.querySelector(".game__area");
const root = game.getContext("2d");

game.width = 600; //window.innerWidth
game.height = window.innerHeight - 10; //window.innerHeight

const gravity = 0.14;

class Bird{
  constructor() {
    this.position = {
      x: game.width / 2,
      y: game.height / 2,
    }
    this.size = {
      width:34,
      height:24,
    }
    this.velocity = {
      x: 0,
      y: 3
    }
    this.jump = {
      height: 0,
      length: 4,
      count: 0,
    }
  }


  draw(){
   root.drawImage(
    createImage(Sprite),
    3,
    490,
    17,
    13,
    this.position.x,
    this.position.y,
    this.size.width,
    this.size.height);
  }

  update(){
    this.draw();
    this.position.y += this.velocity.y - this.jump.height;
    this.jumpBird();
    this.collisionWall();
    this.velocity.y += gravity;
  }

  collisionWall(){
    if(this.position.y + this.size.height + platform.size.height > game.height){
      this.position.y = game.height - this.size.height - platform.size.height; 
    } 
    if(this.position.y - this.size.height < 0){
      this.position.y =  game.height / 2; 
    } 
  }
  jumpBird(){
    if(keys.space.pressed){
      this.jump.count++;
      this.jump.height = 4*this.jump.length*Math.sin(Math.PI*this.jump.count/this.jump.length);
    }
    if(this.jump.count > this.jump.length) {
      this.jump.count = 0;
      this.velocity.y = 0;
      this.jump.height = 0;
      keys.space.pressed = false;
    }
  }
};

class Pipe {
  constructor(x = 0,y = 0,side = true) {
    this.position = {
      x:x,
      y:y
    }
    this.size = {
      width: 60,
      height: 700,
    }
    this.imageSideTop = {
      x: 56,
    }
    this.imageSideBottom = {
      x: 84,
    }
    this.side = side;
    if(this.side) {
      this.imageSide = this.imageSideTop;
    } else {
      this.imageSide = this.imageSideBottom;
    }
    
  }

  draw(){
    root.drawImage(
      createImage(Sprite), 
      this.imageSide.x, 
      323, 
      26, 
      160, 
      this.position.x,
      this.position.y,
      this.size.width,
      this.size.height);
  }
  update(){
    this.draw();
    this.position.x -= 3;
  }
  randomHeight(){
    return Math.floor(Math.random() * ((-120) - (-600) + 1) + (-600));
  }
};

class Platform {
  constructor(){
    this.size = {
      width: game.width,
      height: 75,
    }
    this.position = {
      x:0,
      y:game.height - this.size.height,
    }
  }

  draw(){
    root.drawImage(
      createImage(Sprite), 
      292, 
      0, 
      152, 
      56, 
      this.position.x,
      this.position.y,
      this.size.width,
      this.size.height);
  }
}

class Score {
  constructor(scoreNumber = 0){
    this.position = {
      x: 10,
      y: 50
    }

    this.setup = '50px serif';
    this.scoreNumber = scoreNumber;
  }

  draw(){
    root.font = this.setup;
    root.fillText(this.scoreNumber, this.position.x, this.position.y);
  }
}

//create bird
let bird = new Bird();
//create pipe 
let pipe = new Pipe();
let score = new Score();
let platform = new Platform();

// to check if a user pressed the space bar 
let keys = {
  space: {
    pressed: false,
  },
}; 


// function for creating image
function createImage(imageScr){
  const image = new Image();
  image.src = imageScr;
  return image;
}



// Pairs of pipes replace if their position + their width < than 0 
function replacePairOfPipes(arrayPairOfPipes){
  let currentRandom = Math.floor(Math.random() * ((-120) - (-600) + 1) + (-600));
  if(arrayPairOfPipes[0].position.x + arrayPairOfPipes[0].size.width < 0){
    for(let i = 0; i < arrayPairOfPipes.length; i++) {
      arrayPairOfPipes[i].position.x = 2000;
      arrayPairOfPipes[i].position.y = currentRandom + ( i * 800);
    }
  }
}
// Collision between Bird and Pipes
function ditectionCollisionBirdAndPipes(pipeCollision) {
   
  if( bird.position.x + bird.size.width >= pipeCollision.position.x
    && bird.position.x <= pipeCollision.size.width + pipeCollision.position.x
    && bird.position.y + bird.size.height >= pipeCollision.position.y
    && bird.position.y <= pipeCollision.size.height + pipeCollision.position.y) {
  
    return true;

  } else {

    return false;
    
  }
}
// function for creating five pairs of pipes 
function createPairOfPipes() {
  let pipes = [[],[],[],[],[]];
  let min = -630,
      max = -200;
  for(let j = 0; j < 5; j++) {
    let randomY = Math.floor(Math.random() * ((max) - (min) + 1) + min);
    pipes[j][0] = new Pipe(game.width + ( 200 + (j * 400)), randomY);
    pipes[j][1] = new Pipe(game.width + ( 200 + (j * 400)), randomY + (800),false);
  }
  return pipes;
}
let pairPipes = createPairOfPipes();

//main function 
function animation(){
  requestAnimationFrame(animation);
  root.drawImage(createImage(Sprite), 0, 0, 144, 256, 0, 0, game.width, game.height);
  for(let i = 0; i < 5; i++){
    pairPipes[i].forEach((pipe) => {
      pipe.update();
    });
    replacePairOfPipes(pairPipes[i]);
  }  
  for(let i = 0; i < 5; i++){
    for(let j = 0; j < 2; j++) {
      if(ditectionCollisionBirdAndPipes(pairPipes[i][j])){
        console.log("hit");
      } 
    }
  }
  platform.draw();
  score.draw();
  bird.update();

}

animation();


document.addEventListener('keydown',({keyCode}) => {
  
  switch (keyCode) {
    case 32:
      console.log("space");
      keys.space.pressed = true;
      break;
  }
});

